@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    scroll-behavior: smooth;
}

:root {
    --color-primary: #990000 !important;
    --color-secondary: #ffffff !important;
}

body {
    overflow-x: hidden;
}

* {
    font-family: sans-serif;
    letter-spacing: 1.5px;
}
.title {
    text-align: center;
    margin-bottom: 5px;
    font-weight: bold;
    padding-top: 10px;
    font-size: 2rem;
}

.title h2 {
    color: var(--color-primary);
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 2px;
    margin-bottom: 50px;
}

.title-inverted h2 {
    color: white !important;
    margin-bottom: 50px;
}

.title-bottom {
    background-color: var(--color-primary) !important;
    height: 5px;
    width: 200px;
    margin: auto;
    margin-bottom: 30px;
}

.title-bottom-inverted {
    background-color: var(--color-secondary) !important;
}

@media screen and (max-width: 500px) {
    .title h2 {
        font-size: 1.6rem;
    }
}
.my-card {
    max-width: 80%;
    margin: auto;
    margin-bottom: 30px;
    box-shadow: 0 0 10px 1px lightgrey;
}

.card-body {
    text-align: center !important;
    color: var(--color-primary) !important;
}

.card-body h5 {
    color: var(--color-primary) !important;
    font-weight: 600;
}

.card-body h6 {
    color: var(--color-primary) !important;
    font-weight: 400;
}

.card-body a span {
    color: #ffffff;
}

.card-img-top {
    border-bottom: 5px solid var(--color-primary);
}

.card-body a {
    background-color: var(--color-primary);
}

.card-body a.btn:hover {
    background-color: #ffffff;
    border: 1px solid;
    border-color: var(--color-primary);
}

.card-body a.btn:focus {
    box-shadow: none !important;
}

.card-body a.btn:hover .btntext {
    color: var(--color-primary);
}

@media screen and (max-width: 767px) {
    .my-card {
        max-width: 60%;
        margin: auto;
        margin-bottom: 30px;
    }
}

@media screen and (max-width: 500px) {
    .card-body h5 {
        font-size: 18px;
    }
}
@media screen and (max-width: 683px) {
    #my-projects {
        scroll-margin-top: 95px;
    }
}

.pagination {
    display: flex;
    justify-content: center;
    color: var(--color-primary);
}

.pagination * {
    color: var(--color-primary);
    border-color: var(--color-primary);
    outline: none;
}

.projects-bottom-svg {
    margin-bottom: -1px;
}
.fa {
    font-size: 35px !important;
}

div p {
    font-size: 20px;
    margin-top: 5px;
    text-decoration: none !important;
}

.main {
    margin-bottom: 30px;
}
.contact-me {
    color: var(--color-secondary);
    text-align: center;
    background-color: var(--color-primary);
}

.footer {
    padding-bottom: 15px;
    font-weight: lighter;
}
.work-tab {
    border: 5px solid;
    border-color: var(--color-secondary);
    width: 60%;
    margin: auto;
    margin-bottom: 20px;
    background-color: var(--color-secondary);
    color: var(--color-primary);
    font-weight: bold;
    border-radius: 20px;
    min-height: 350px;
}

ul {
    font-size: 20px;
    font-weight: 300;
}

.work-tab h4,
h5 {
    text-align: center;
    color: var(--color-primary);
}

.work-tab h4 {
    font-weight: 600;
}

.work-tab h5 {
    font-weight: 400;
    font-size: 22px;
}

@media screen and (max-width: 991px) {
    .work-tab {
        width: 80%;
        min-height: 0;
    }
}

@media screen and (max-width: 500px) {
    .work-tab h4 {
        font-size: 20px;
        max-width: 90%;
        margin: 0 auto;
    }
    .work-tab h5 {
        font-size: 18px;
    }
    .work-tab {
        width: 90%;
        min-height: 0;
    }
    ul {
        font-size: 16px;
    }
}
    .experience {
        background-color: var(--color-primary);
        margin-top: -10px;
    }
    
    .experience .work-row {
        display: none;
    }
    
    .svg {
        margin-top: -1px;
    }
    
    @media screen and (max-width: 683px) {
        .experience {
            scroll-margin-top: 95px;
        }
    }
    
    @media screen and (max-width: 991px) {
        .experience .work-row {
            display: block;
        }
        #carouselExampleIndicators {
            display: none;
        }
        .experience {
            padding-bottom: 10px;
        }
    }
    
    @media screen and (max-width: 500px) {
        .experience {
            scroll-margin-top: 55px;
        }
        .experience h2 {
            font-size: 1.6rem;
        }
    }
.profile {
    align-items: center;
    padding-top: 10vw;
    padding-bottom: 5vw;
    margin-top: 0px;
    background-color: var(--color-secondary);
}

.profile p,
h3 {
    color: var(--color-primary);
    margin: auto;
    text-align: center;
    margin-bottom: 15px;
}

.profile h3 {
    width: 60%;
}

.profile p {
    -webkit-animation: animated-text 4s steps(29, end) 1s 1 normal both, animated-cursor 600ms steps(29, end) infinite;
            animation: animated-text 4s steps(29, end) 1s 1 normal both, animated-cursor 600ms steps(29, end) infinite;
    white-space: nowrap;
    overflow: hidden;
    border-right: solid 3px var(--color-primary);
    width: 470px;
}

@-webkit-keyframes animated-text {
    from {
        width: 0;
    }
}

@keyframes animated-text {
    from {
        width: 0;
    }
}

@-webkit-keyframes animated-cursor {
    from {
        border-right-color: var(--color-primary);
    }
    to {
        border-right-color: transparent;
    }
}

@keyframes animated-cursor {
    from {
        border-right-color: var(--color-primary);
    }
    to {
        border-right-color: transparent;
    }
}

.profile h3 {
    width: 75%;
    margin-top: 30px;
}

.profile p {
    font-weight: bold;
    font-size: 60px;
}

.profile h3 {
    font-weight: 300;
    font-size: 30px;
}

.profile-btntext {
    color: var(--color-secondary);
}

.profile-btn {
    background-color: var(--color-primary);
    padding: 15px;
    display: block;
    margin: auto;
    width: 200px;
    margin-top: 30px;
    border-radius: 5px;
    color: var(--color-secondary);
}

.profile-btn:hover {
    text-decoration: none;
    background-color: #ffffff;
    border: 1px solid;
    border-color: var(--color-primary);
    color: var(--color-primary);
}

.svg {
    margin-top: -1px;
}

@media screen and (max-width: 700px) {
    .profile h3 {
        width: 100%;
    }
    .profile p {
        width: 320px;
    }
}

@media screen and (max-width: 683px) {
    #my-projects {
        scroll-margin-top: 95px;
    }
}

@media screen and (max-width: 500px) {
    #my-projects {
        scroll-margin-top: 55px;
    }
    .profile p {
        font-size: 40px;
    }
    .profile h3 {
        font-size: 22px;
    }
    .profile {
        padding-top: 20vw;
        padding-bottom: 10vw;
    }
    .profile-btn {
        padding: 10px;
        width: 180px;
    }
    .profile h3 {
        width: 90%;
    }
}
#myBtn {
    position: fixed;
    bottom: 50px;
    right: 5px;
    z-index: 99999;
    outline: none;
    background-color: var(--color-secondary);
    color: var(--color-primary);
    cursor: pointer;
    height: 50px;
    width: 50px;
    justify-content: space-around;
    align-items: center;
    border-radius: 50%;
    display: flex;
    border: 1px solid;
    border-color: var(--color-primary);
}

.fa {
    font-size: 25px !important;
}

#myBtn:active {
    background-color: var(--color-primary);
    color: var(--color-secondary);
}

.header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
}

.progress-container {
    background-color: var(--color-secondary);
    height: 10px;
    width: 100%;
}

.progressBar {
    background-color: var(--color-primary);
    height: 10px;
    width: 0%;
}
